import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/views/Home";

Vue.use(VueRouter);

const routes = [
  { path: "/", component: Home, name: "Home", icon: "mdi-home" }
  // { path: '/bar', component: Bar }
];
const router = new VueRouter({
  routes,
  mode: "history"
});

export default router;
