<template>
  <div>
    <div>
      <span style="text-decoration: overline">
        <span style="text-decoration: overline">A</span>+
        <span style="text-decoration: overline">B</span>
      </span>
      +
      <span>C</span>
    </div>
    <v-row>
      <v-col>
        <v-select :items="ajuda" label="Ajuda"></v-select>
      </v-col>
      <v-col>
        <v-select :items="sintaxe" label="Sintaxe" v-model="bools"></v-select>
      </v-col>
      <v-col>
        <v-text-field
          type="number"
          min="1"
          max="8"
          label="Num.Variáveis"
          v-model="qtd"
        ></v-text-field>
      </v-col>
      <v-col>
        <v-text-field label="Expressão" v-model="formula"></v-text-field>
      </v-col>
      <v-col>
        <v-btn block @click="clicou">Nova Coluna</v-btn>
      </v-col>
    </v-row>
    <table :key="refresh">
      <tr>
        <th class="tdid">id</th>
        <th class="thvars" v-for="(v, i) of variaveis" :key="i">{{ v }}</th>
        <th v-for="(col, formula) in cols" :key="formula">
          <span v-html="formatFormula(formula)"></span>
          <!-- {{formatFormula(formula)}} -->
          <v-btn icon small color="#F77" @click="removeColuna(formula)">
            <v-icon>mdi-trash-can</v-icon>
          </v-btn>
        </th>
      </tr>
      <tr v-for="(row, i) of lista" :key="i">
        <td class="tdid">{{ row["id"] }}</td>
        <td class="tdvars" v-for="(v, j) in variaveis" :key="j">
          {{ boolOf(row[v]) }}
        </td>
        <td v-for="(col, formula) in cols" :key="formula">
          {{ cloze ? false : respOf(col[i]) }}
        </td>
      </tr>
    </table>
    <br />
    <v-alert color="red lighten-2" dark v-show="error_enable">
      {{ error }}
      <v-btn icon @click="error_enable = false"
        ><v-icon>mdi-close-circle</v-icon></v-btn
      >
    </v-alert>
  </div>
</template>

<script>
export default {
  data: () => ({
    cloze: false,
    sintaxe: [
      {
        text: "[0,1]",
        value: [
          [0, 1],
          [0, 1],
        ],
      },
      {
        text: "[F,V]",
        value: [
          ["F", "V"],
          ["F", "V"],
        ],
      },
      { divider: true },
      {
        text: "CLOZE [0,1]",
        value: [
          [0, 1],
          ["{1:MULTICHOICE:~%100%0~1}", "{1:MULTICHOICE:~0~%100%1}"],
        ],
      },
      {
        text: "CLOZE [F,V]",
        value: [
          ["F", "V"],
          ["{1:MULTICHOICE:~%100%F~V}", "{1:MULTICHOICE:~F~%100%V}"],
        ],
      },
    ],
    variaveis: [],
    lista: [],
    cols: {},
    formula: "A+B",
    refresh: 0,
    qtd: 2,
    bools: [
      [0, 1],
      [0, 1],
    ],
    ajuda: [
      "not: ! ~ ¬",
      "and: ∧ ·",
      "or: ∨ +",
      "n vira ¬",
      "xor vira ⊕",
      "^ vira ⋀",
      "v vira ∨",
      "-> vira →",
      "<> ou <-> vira ↔",
    ],
    error: "",
    error_enable: false,
  }),
  created() {
    this.variaveis = ["A", "B"];
    this.createCombinations(this.variaveis);
    this.addColuna("A.B");
    this.addColuna("n(AvB)");
  },
  computed: {},
  watch: {
    qtd() {
      this.variaveis = new Array(parseInt(this.qtd, 10))
        .fill(0)
        .map((x, i) => String.fromCharCode(65 + i));
      this.createCombinations(this.variaveis);
      this.recalcColunas();
    },
  },
  methods: {
    formatFormula(formula) {
      formula = formula.replace(
        /\[/g,
        '<span style="text-decoration: overline;">'
      );
      formula = formula.replace(/\]/g, "</span>");
      return formula;
    },
    boolOf(value) {
      return this.bools[0][value];
    },
    respOf(value) {
      return this.bools[1][value];
    },
    clicou() {
      this.addColuna(this.formula);
      this.refresh = Math.random();
    },
    recalcColunas() {
      for (let formula in this.cols) {
        this.addColuna(formula);
      }
    },
    createCombinations(varArray) {
      let m = varArray.length;
      let n = 2 ** m;
      let reverso = [...varArray].reverse();
      this.lista = new Array(n)
        .fill()
        .map((row, index) => binaryLine(reverso, index));
      function binaryLine(vars, num) {
        let entries = vars.map((v, k) => [v, num & (1 << k) ? 1 : 0]);
        entries.push(["id", num]);
        return Object.fromEntries(entries);
      }
    },
    addColuna(formula) {
      // https://www.compart.com/en/unicode/block/U+2200
      // console.log(formula);
      formula = formula.replace(/ /g, "");
      formula = formula.replace(/\^/g, "⋀");
      formula = formula.replace(/v/gi, "⋁");
      formula = formula.replace(/<>/g, "↔");
      formula = formula.replace(/<->/g, "↔");
      formula = formula.replace(/->/g, "→");
      formula = formula.replace(/\./g, "·");
      formula = formula.replace(/\+/g, "+");
      formula = formula.replace(/xor/g, "⊕");
      formula = formula.replace(/n/g, "¬");

      try {
        this.cols[formula] = this.lista.map((row) =>
          this.calcula(row, formula)
        );
      } catch (e) {
        this.error = e;
        this.error_enable = true;
        console.log(e);
      }
    },
    removeColuna(formula) {
      delete this.cols[formula];
      this.refresh = Math.random();
    },
    calcula(input, formula) {
      let v1,
        v2,
        i = 0;
      let pilha = [];
      expressao();
      if (i !== formula.length) throw new Error("Expressão Inválida");
      return pilha.pop();

      // Expressão -> Termo 'Operador' Expressão | Termo
      // Termo -> Fator 'Operador' Termo | Fator
      // Fator -> Num | (Expressão)
      // Num -> 0..9 | 0..9 Num
      function expressao() {
        somas();
        if (match("→")) {
          expressao();
          v2 = pilha.pop();
          v1 = pilha.pop();
          pilha.push(!v1 | v2);
        } else if (match("↔")) {
          expressao();
          v2 = pilha.pop();
          v1 = pilha.pop();
          let k = !(v1 ^ v2);
          pilha.push(k ? 1 : 0);
        }
      }
      function somas() {
        multiplic();
        if (match("+") || match("⋁")) {
          somas();
          v2 = pilha.pop();
          v1 = pilha.pop();
          pilha.push(v1 | v2);
        } else if (match("⊕")) {
          somas();
          v2 = pilha.pop();
          v1 = pilha.pop();
          let k = v1 ^ v2;
          pilha.push(k ? 1 : 0);
        }
      }
      function multiplic() {
        negacao();
        if (match("·") || match("⋀")) {
          multiplic();
          v2 = pilha.pop();
          v1 = pilha.pop();
          pilha.push(v1 & v2);
        }
      }
      function negacao() {
        if (match("!") || match("~") || match("¬")) {
          parenteses();
          v1 = pilha.pop();
          pilha.push(v1 ? 0 : 1);
        } else if (match("[")) {
          expressao();
          if (!match("]")) throw new Error("] experado");
          v1 = pilha.pop();
          pilha.push(v1 ? 0 : 1);
        } else {
          parenteses();
        }
      }
      function parenteses() {
        if (match("(")) {
          expressao();
          if (!match(")")) throw new Error(" ) experado");
        } else {
          value();
        }
      }
      function value() {
        let variavel = formula[i];
        if (variavel in input) {
          pilha.push(input[variavel]);
          i++;
        } else {
          throw new Error(`variavel ${variavel} não econtrada em ${input}`);
        }
      }
      function match(x) {
        if (formula[i] === x) {
          i++;
          return true;
        }
        return false;
      }
    },
  },
};
</script>

<style scoped>
th {
  background-color: lightblue;
}
th,
td {
  font-family: Arial, sans-serif;
  font-size: 14px;
  min-width: 3em;
  border: 1px solid black;
  text-align: center !important;
}
.tdid {
  background-color: transparent;
  border: none;
}
.thvars {
  background-color: rgb(255, 205, 140);
}
.tdvars {
  background-color: rgb(252, 238, 221);
}
</style>